'use strict';

import frame from './modules/frame';
import video from './modules/video';
import gallery from './modules/gallery';
import inputs from './modules/inputs';
import modal from './modules/modal';
import tickets from './modules/tickets';
import burger from './modules/burger';


window.addEventListener('DOMContentLoaded', () => {
    frame('.visiting__item a', '.frame', '.frame__close');
    video('.video__controls input[type="range"]');
    gallery('.gallery__col-one','.gallery__col-two','.gallery__col-three');
    inputs();
    modal('.tickets__amount-submit', '.overlay__close', '.overlay', '.overlay__window', 'overlay__window_active');
    tickets('.tickets__amount-input-block','.tickets__amount-btn');
    tickets('.overlay__num-block','button');
    burger('.header__burger','.welcome__nav','header__burger_click','welcome__nav_active');
});
